































import { createComponent } from "@vue/composition-api";

export default createComponent({
  name: "app-result",
  setup(props, ctx) {
    const changePanel = () => {
      ctx.root.$store.commit("setPanel");
    };
    const colors = ["#99A9BF", "#F7BA2A", "#FF9900"];
    return {
      colors,
      changePanel
    };
  }
});
